import { Component, OnInit } from '@angular/core';
import { Games } from '../game/highscore.list';
import { HiraganaMultipleChoiceGame } from '../game/hiragana-multiple-choice/hiragana-multiple-choice.game.';
import { NihongoGame, Statuses } from '../game/nihongo-base/nihongo-game';
import { FuriganaModel } from '../model/furigana.model';

@Component({
  selector: 'app-hiragana-multiple-choice',
  templateUrl: './hiragana-multiple-choice.component.html',
  styleUrls: ['./hiragana-multiple-choice.component.scss']
})
export class HiraganaMultipleChoiceComponent implements OnInit {
  public hiraganaGame: NihongoGame = new HiraganaMultipleChoiceGame();

  constructor() { }

  ngOnInit(): void { }

  get multipleChoiceValue(): FuriganaModel[] {
    return this.hiraganaGame.multipleChoiceValue;
  }

  get givenHiragana(): string {
    return this.hiraganaGame.getGiven()?.hiragana ?? '';
  }

  get hasStarted(): boolean {
    return this.hiraganaGame.statusValue === Statuses.Started;
  }

  get hasCompleted(): boolean {
    return this.hiraganaGame.statusValue === Statuses.Completed;
  }

  get onMenu(): boolean {
    return this.hiraganaGame.statusValue === Statuses.Menu;
  }

  get completed(): boolean {
    return this.hiraganaGame.statusValue === Statuses.Completed;
  }

  get highscore(): boolean {
    return this.hiraganaGame.statusValue === Statuses.Highscore;
  }

  get game(): Games {
    return Games.HiraganaMultiple;
  }

  isEnabled(): boolean {
    return this.hiraganaGame.hasStarted;
  }

  isVisible(): boolean {
    return this.hiraganaGame.hasStarted;
  }

  onSelect(furigana: FuriganaModel): void {
    this.hiraganaGame.checkSelected(furigana);
  }
}
