import { FuriganaModel } from '../../model/furigana.model';
import { FuriganaDb } from '../../static-db/furigana.db';
import { Games, HighscoreList, HighscoreModel } from '../highscore.list';
import { HiraganaRomajiTimerScore } from '../hiragana-romaji-timer.score';
import { NihongoGame, Statuses } from '../nihongo-base/nihongo-game';
import { NihongoGameBase } from '../nihongo-base/nihongo-game.base';
import { NihongoScoreboard } from '../nihongo-base/nihongo-scoreboard';

export class KatakanaMultipleChoiceGame extends NihongoGameBase {
  protected table: FuriganaModel[] = FuriganaDb.randomKatakana();
  protected scoreboard: NihongoScoreboard = new NihongoScoreboard(new HiraganaRomajiTimerScore());

  start(): void {
    this.selected = [];
    this.table = FuriganaDb.randomKatakana(20);
    this.scoreboard = new NihongoScoreboard(new HiraganaRomajiTimerScore());
    this.status = Statuses.Started;
    this.scoreboard.start();
    this.scoreboard.setGiven(this.chooseFurigana());
    this.multipleChoice = this.getMultipleChoice();
  }

  checkSelected(chosen: FuriganaModel): void {
    if (chosen.romaji === this.scoreboard.givenValue?.romaji) {
      this.addSelected(chosen);
      this.scoreboard.addPoint();
      this.scoreboard.setGiven(this.chooseFurigana());
      this.multipleChoice = this.getMultipleChoice();
      if (this.table.length === this.selected.length) {
        this.status = Statuses.Completed;
        this.saveHighscore();
        this.scoreboard.stop();
      }
    } else {
      this.scoreboard.missed();
    }
  }

  protected saveHighscore(): void {
    const highscore = new HighscoreModel(this.getPoint(), this.getTimeValue(), this.getError());
    HighscoreList.addHighscore(highscore, Games.KatakanaMultiple);
  }
}
