import { NihongoScore } from './nihongo-base/nihongo-score';

export class HiraganaRomajiTimerScore implements NihongoScore {
    readonly missedPoint = 50;

    getPoint(lastTime: number, currentTime: number): number {
        let diff = currentTime - lastTime - 1;
        diff = diff < 0 ? 0 : diff;

        const point = 100 - (10 * diff);

        return point < 20 ? 20 : point;
    }

    getMissedPoint(): number {
        return this.missedPoint;
    }
}
