import { Component, Input, OnInit } from '@angular/core';
import { NihongoGame } from 'src/app/game/nihongo-base/nihongo-game';

@Component({
  selector: 'app-game-scoreboard',
  templateUrl: './game-scoreboard.component.html',
  styleUrls: ['./game-scoreboard.component.scss']
})
export class GameScoreboardComponent implements OnInit {
  @Input() nihongoGame?: NihongoGame;
  @Input() type = 'romaji';

  constructor() { }

  ngOnInit(): void {
  }

  get timeBoard(): string {
    return this.nihongoGame?.getTime() ?? '00:00:00';
  }

  get givenBoard(): string {
    if (this.type === 'romaji') {
      return this.nihongoGame?.getGiven()?.romaji ?? '';
    } else {
      return this.nihongoGame?.getGiven()?.hiragana ?? '';
    }
  }

  get errorBoard(): number {
    return this.nihongoGame?.getError() ?? 0;
  }

  get pointBoard(): number {
    return this.nihongoGame?.getPoint() ?? 0;
  }
}
