import { FuriganaModel } from '../../model/furigana.model';
import { FuriganaDb } from '../../static-db/furigana.db';
import { Games, HighscoreList, HighscoreModel } from '../highscore.list';
import { HiraganaRomajiTimerScore } from '../hiragana-romaji-timer.score';
import { NihongoGame, Statuses } from '../nihongo-base/nihongo-game';
import { NihongoScoreboard } from '../nihongo-base/nihongo-scoreboard';

export class NihongoGameBase implements NihongoGame {
  protected table: FuriganaModel[] = FuriganaDb.randomHiragana();
  protected scoreboard: NihongoScoreboard = new NihongoScoreboard(new HiraganaRomajiTimerScore());
  protected selected: FuriganaModel[] = [];
  protected status = Statuses.Menu;
  protected multipleChoice: FuriganaModel[] = [];

  constructor() { }

  start(): void {
    this.selected = [];
    this.table = FuriganaDb.randomHiragana(20);
    this.scoreboard = new NihongoScoreboard(new HiraganaRomajiTimerScore());
    this.status = Statuses.Started;
    this.scoreboard.start();
    this.scoreboard.setGiven(this.chooseFurigana());
    this.multipleChoice = this.getMultipleChoice();
  }

  goToMenu(): void {
    this.status = Statuses.Menu;
  }

  goToHighscore(): void {
    this.status = Statuses.Highscore;
  }

  checkSelected(chosen: FuriganaModel): void {
    if (chosen.romaji === this.scoreboard.givenValue?.romaji) {
      this.addSelected(chosen);
      this.scoreboard.addPoint();
      this.scoreboard.setGiven(this.chooseFurigana());
      this.multipleChoice = this.getMultipleChoice();
      if (this.table.length === this.selected.length) {
        this.status = Statuses.Completed;
        this.saveHighscore();
        this.scoreboard.stop();
      }
    } else {
      this.scoreboard.missed();
    }
  }

  getTimeValue(): number {
    return this.scoreboard.timeNumber;
  }

  getTable(): FuriganaModel[] {
    return this.table;
  }

  getSelected(): FuriganaModel[] {
    return this.selected.map((furigana) => furigana.duplicate());
  }

  getTime(): string {
    return this.scoreboard.timeValue;
  }

  getPoint(): number {
    return this.scoreboard.pointValue;
  }

  getError(): number {
    return this.scoreboard.errorValue;
  }

  getGiven(): FuriganaModel | null {
    return this.scoreboard.givenValue;
  }

  get statusValue(): Statuses {
    return this.status;
  }

  get multipleChoiceValue(): FuriganaModel[] {
    return this.multipleChoice;
  }

  get hasStarted(): boolean {
    return this.status === Statuses.Started;
  }

  get onMenu(): boolean {
    return this.status === Statuses.Menu;
  }

  protected saveHighscore(): void {
    const highscore = new HighscoreModel(this.getPoint(), this.getTimeValue(), this.getError());
    HighscoreList.addHighscore(highscore, Games.HiraganaMultiple);
  }

  protected getMultipleChoice(): FuriganaModel[] {
    const furiganaSet: FuriganaModel[] = [];
    const givenIndex = Math.floor(Math.random() * 4);
    while (furiganaSet.length < 4) {
      if (furiganaSet.length === givenIndex) {
        if (this.scoreboard.givenValue){
          furiganaSet.push(this.scoreboard.givenValue);
        }
      }

      const furigana = this.randomFurigana(furiganaSet);
      if (furigana) {
        furiganaSet.push(furigana);
      }

      if (furiganaSet.length === givenIndex) {
        if (this.scoreboard.givenValue){
          furiganaSet.push(this.scoreboard.givenValue);
        }
      }
    }

    return furiganaSet;
  }

  protected addSelected(furigana: FuriganaModel): void {
    this.selected.push(furigana.duplicate());
  }

  protected chooseFurigana(): FuriganaModel {
    const table = this.table.filter((f) => {
      const selected = this.selected.filter((s) => s.romaji === f.romaji);
      return selected.length === 0;
    });
    const index = Math.floor(Math.random() * table.length);
    return table[index];
  }

  protected randomFurigana(furiganaSet: FuriganaModel[]): FuriganaModel {
    const given = this.scoreboard.givenValue;
    const table = this.table.filter((f) => {
      const set = furiganaSet.filter((fs) => fs.romaji === f.romaji);
      return set.length === 0 && f.romaji !== given?.romaji;
    });
    const index = Math.floor(Math.random() * table.length);
    return table[index];
  }
}
