import { Component, Input, OnInit } from '@angular/core';
import { NihongoGame } from 'src/app/game/nihongo-base/nihongo-game';

@Component({
  selector: 'app-game-final-score',
  templateUrl: './game-final-score.component.html',
  styleUrls: ['./game-final-score.component.scss']
})
export class GameFinalScoreComponent implements OnInit {
  @Input() nihongoGame?: NihongoGame;

  constructor() { }

  get score(): number {
    return this.nihongoGame?.getPoint() ?? 0;
  }

  get time(): string {
    return this.nihongoGame?.getTime() ?? '00:00:00';
  }

  get missed(): number {
    return this.nihongoGame?.getError() ?? 0;
  }

  ngOnInit(): void {
  }

  goToMenu(): void {
    this.nihongoGame?.goToMenu();
  }
}
