import { interval, Subscription } from 'rxjs';
import { FuriganaModel } from '../../model/furigana.model';
import { NihongoScore } from './nihongo-score';

export class NihongoScoreboard {
  private lastTime = 0;
  private time = 0;
  private points = 0;
  private errors = 0;
  private given: FuriganaModel | null = null;
  private timerSub: Subscription | null = null;
  private scoreConfig: NihongoScore | null = null;

  constructor(scoreConfig: NihongoScore) {
    this.lastTime = 0;
    this.time = 0;
    this.points = 0;
    this.errors = 0;
    this.given = null;
    this.timerSub = null;
    this.scoreConfig = scoreConfig;
  }

  start(): void {
    this.timerSub = interval(1000)
      .subscribe(x => { this.countdown(); });
  }

  stop(): void {
    this.timerSub?.unsubscribe();
  }

  missed(): void {
    this.errors++;
    this.points -= this.scoreConfig?.getMissedPoint() ?? 0;
  }

  addPoint(): void {
    this.points += this.scoreConfig?.getPoint(this.lastTime, this.time) ?? 0;
    this.lastTime = this.time;
  }

  setGiven(furigana: FuriganaModel): void {
    this.given = furigana;
  }

  get timeNumber(): number {
    return this.time;
  }

  get timeValue(): string {
    const hour = Math.floor(this.time / 3600);
    const minute = Math.floor((this.time % 3600) / 60);
    const seconds = Math.floor((this.time % 3600) % 60);

    return `${this.min2digit(hour)}:${this.min2digit(minute)}:${this.min2digit(seconds)}`;
  }

  get pointValue(): number {
    return this.points;
  }

  get errorValue(): number {
    return this.errors;
  }

  get givenValue(): FuriganaModel | null {
    return this.given;
  }

  private countdown(): void {
    this.time++;
  }

  private min2digit(num: number): string {
    if (num < 10) {
      return `0${num}`;
    } else {
      return `${num}`;
    }
  }
}
