import { Component, OnInit } from '@angular/core';
import { AppGlobal } from './game/app-global';
import { Games, HighscoreList } from './game/highscore.list';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'nihongo-torena';
  availableGames = Games;

  ngOnInit(): void {
    HighscoreList.getHighscores();
  }

  selectGame(selection: Games): void {
    AppGlobal.gameSelected = selection;
  }

  get selected(): Games {
    return AppGlobal.gameSelected;
  }
}
