import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FuriganaModel } from 'src/app/model/furigana.model';

@Component({
  selector: 'app-hiragana-tile',
  templateUrl: './hiragana-tile.component.html',
  styleUrls: ['./hiragana-tile.component.scss']
})
export class HiraganaTileComponent implements OnInit {
  @Input() furigana: FuriganaModel = new FuriganaModel('n');
  @Input() isEnabled = false;
  @Input() isVisible = true;
  @Output() selected: EventEmitter<FuriganaModel> = new EventEmitter();

  constructor() { }

  ngOnInit(): void {
  }

  onSelect(): void {
    if (this.isEnabled){
      this.selected.emit(this.furigana);
    }
  }

  get romaji(): string {
    return this.furigana.romaji;
  }

  get hiragana(): string {
    return this.furigana.hiragana;
  }
}
