import { FuriganaModel } from '../../model/furigana.model';

export enum Statuses {
  Stopped,
  Started,
  Menu,
  Completed,
  Failed,
  Highscore
}

export interface NihongoGame {
  hasStarted: boolean;
  onMenu: boolean;
  multipleChoiceValue: FuriganaModel[];
  statusValue: Statuses;
  start(): void;
  getTable(): FuriganaModel[];
  checkSelected(chosen: FuriganaModel): void;
  getSelected(): FuriganaModel[];
  getTime(): string;
  getTimeValue(): number;
  getGiven(): FuriganaModel | null;
  getPoint(): number;
  getError(): number;
  goToMenu(): void;
  goToHighscore(): void;
}
