import { Component, Input, OnInit } from '@angular/core';
import { Games, HighscoreList, HighscoreModel } from 'src/app/game/highscore.list';
import { NihongoGame } from 'src/app/game/nihongo-base/nihongo-game';

@Component({
  selector: 'app-game-highscore-list',
  templateUrl: './game-highscore-list.component.html',
  styleUrls: ['./game-highscore-list.component.scss']
})
export class GameHighscoreListComponent implements OnInit {
  @Input() nihongoGame?: NihongoGame;
  @Input() game?: Games;
  highscoreList: HighscoreModel[] = [];

  constructor() { }

  ngOnInit(): void {
    console.log(HighscoreList.highscores);
    if (this.game){
      this.highscoreList = HighscoreList.highscores.find((h) => h.key === this.game)?.highscores ?? [];
    }
  }

  goToMenu(): void {
    this.nihongoGame?.goToMenu();
  }
}
