import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AppGlobal } from 'src/app/game/app-global';
import { Games } from 'src/app/game/highscore.list';

@Component({
  selector: 'app-menu-button',
  templateUrl: './menu-button.component.html',
  styleUrls: ['./menu-button.component.scss']
})
export class MenuButtonComponent implements OnInit {
  @Output() started: EventEmitter<void> = new EventEmitter();
  @Output() highscore: EventEmitter<void> = new EventEmitter();

  constructor() { }

  ngOnInit(): void {
  }

  startGame(): void {
    this.started.emit();
  }

  goToHighscore(): void {
    this.highscore.emit();
  }

  goToGameMenu(): void {
    AppGlobal.gameSelected = Games.NoSelection;
  }
}
