import { Component, OnInit } from '@angular/core';
import { Games } from '../game/highscore.list';
import { KatakanaMultipleChoiceGame } from '../game/katakana-multiple-choice/katakana-multiple-choice.game';
import { NihongoGame, Statuses } from '../game/nihongo-base/nihongo-game';
import { FuriganaModel } from '../model/furigana.model';

@Component({
  selector: 'app-katakana-multiple-choice',
  templateUrl: './katakana-multiple-choice.component.html',
  styleUrls: ['./katakana-multiple-choice.component.scss']
})
export class KatakanaMultipleChoiceComponent implements OnInit {
  public katakanaGame: NihongoGame = new KatakanaMultipleChoiceGame();

  constructor() { }

  ngOnInit(): void { }

  get multipleChoiceValue(): FuriganaModel[] {
    return this.katakanaGame.multipleChoiceValue;
  }

  get givenKatakana(): string {
    return this.katakanaGame.getGiven()?.katakana ?? '';
  }

  get hasStarted(): boolean {
    return this.katakanaGame.statusValue === Statuses.Started;
  }

  get hasCompleted(): boolean {
    return this.katakanaGame.statusValue === Statuses.Completed;
  }

  get onMenu(): boolean {
    return this.katakanaGame.statusValue === Statuses.Menu;
  }

  get completed(): boolean {
    return this.katakanaGame.statusValue === Statuses.Completed;
  }

  get highscore(): boolean {
    return this.katakanaGame.statusValue === Statuses.Highscore;
  }

  get game(): Games {
    return Games.KatakanaMultiple;
  }

  isEnabled(): boolean {
    return this.katakanaGame.hasStarted;
  }

  isVisible(): boolean {
    return this.katakanaGame.hasStarted;
  }

  onSelect(furigana: FuriganaModel): void {
    this.katakanaGame.checkSelected(furigana);
  }
}
