import { Component, Input, OnInit } from '@angular/core';
import { HiraganaRomajiTimerGame } from 'src/app/game/hiragana-romaji-timer.game';
import { NihongoGame } from 'src/app/game/nihongo-base/nihongo-game';

@Component({
  selector: 'app-game-menu',
  templateUrl: './game-menu.component.html',
  styleUrls: ['./game-menu.component.scss']
})
export class GameMenuComponent implements OnInit {
  @Input() nihongoGame?: NihongoGame;
  @Input() title = 'Guess the Hiragana';
  @Input() subtitle = 'Choose the hiragana based on given romaji';

  constructor() { }

  ngOnInit(): void {
  }

  startGame(): void {
    this.nihongoGame?.start();
  }

  goToHighscore(): void {
    this.nihongoGame?.goToHighscore();
  }
}
