import { toHiragana, toKatakana } from 'wanakana';

export class FuriganaModel {
    romaji: string;
    hasHiragana: boolean;
    hasKatakana: boolean;

    public static toHiragana(romaji: string): string {
        return toHiragana(romaji, {
            customKanaMapping: {
                dzi: 'ぢ', dzu: 'づ', dzya: 'ぢゃ', dzyu: 'ぢゅ', dzyo: 'ぢょ'
            },
        });
    }

    public static toKatakana(romaji: string): string {
        return toKatakana(romaji, {
            customKanaMapping: {
                dzi: 'ヂ', dzu: 'ヅ', dzya: 'ヂャ', dzyu: 'ヂュ', dzyo: 'ヂョ',
                ē: 'エー', kē: 'ケー', sē: 'セー', tē: 'テー', nē: 'ネー', hē: 'ヘー',
                mē: 'メー', rē: 'レー', ā: 'アー', kā: 'カー', sā: 'サー', tā: 'ター',
                nā: 'ナー', hā: 'ハー', mā: 'マー', yā: 'ヤー', rā: 'ラー', wā: 'ワー'
            },
        });
    }

    constructor(romaji: string, hasKatakana = true, hasHiragana = true){
        this.romaji = romaji;
        this.hasKatakana = hasKatakana;
        this.hasHiragana = hasHiragana;
    }

    public get hiragana(): string {
        return FuriganaModel.toHiragana(this.romaji);
    }

    public get katakana(): string {
        return FuriganaModel.toKatakana(this.romaji);
    }

    public duplicate(): FuriganaModel {
        return new FuriganaModel(this.romaji, this.hasKatakana, this.hasHiragana);
    }
}
