export enum Games {
  HiraganaMultiple = 'HiraganaMultiple',
  KatakanaMultiple = 'KatakanaMultiple',
  NoSelection = 'NoSelection'
}

export class HighscoreList {
  public static highscores: HighscoreBank[] = [];

  public static getHighscores(): void {
    const myHighscore: HighscoreBank[] = [];
    const item = localStorage.getItem('highscores');
    const highscoreBank: any[] = JSON.parse(item ?? '[]');

    highscoreBank.forEach((h) => {
      const key = h.key;
      const highscores = h.highscores;

      if (Object.keys(Games).includes(key) && Array.isArray(highscores)) {
        const eachHighscore: HighscoreModel[] = [];
        highscores.forEach((hs) => {
          const point = hs.point;
          const time = hs.time;
          const missed = hs.missed;

          if (!isNaN(point) && !isNaN(time) && !isNaN(missed) && isNaN(key)) {
            eachHighscore.push(new HighscoreModel(point, time, missed));
          }
        });
        myHighscore.push(new HighscoreBank(key, eachHighscore));
      }
    });

    this.highscores = myHighscore;
  }

  public static addHighscore(highscore: HighscoreModel, key: Games): void {
    const game = this.highscores.find((h) => h.key === key) ?? new HighscoreBank(key, []);
    game.highscores.push(highscore);
    game.highscores.sort((a, b) => {
      if (a.point === b.point) {
        if (a.time === b.time) {
          if (a.missed === b.missed) {
            return 0;
          } else if (a.missed < b.missed) {
            return 1;
          } else {
            return -1;
          }
        } else if (a.time < b.time) {
          return 1;
        } else {
          return -1;
        }
      } else if (a.point < b.point) {
        return 1;
      } else {
        return -1;
      }
    });

    if (game.highscores.length > 10) {
      game.highscores = game.highscores.slice(0, 10);
    }

    if (this.highscores.findIndex((h) => h.key === key) === -1){
      this.highscores.push(game);
    }

    this.saveHighscore();
  }

  public static saveHighscore(): void {
    localStorage.setItem('highscores', JSON.stringify(this.highscores));
  }
}

export class HighscoreBank {
  key: Games;
  highscores: HighscoreModel[];

  constructor(key: Games, highscores: HighscoreModel[]) {
    this.key = key;
    this.highscores = highscores;
  }
}

export class HighscoreModel {
  point: number;
  time: number;
  missed: number;

  constructor(point: number, time: number, missed: number) {
    this.point = point;
    this.time = time;
    this.missed = missed;
  }

  get timeValue(): string {
    const hour = Math.floor(this.time / 3600);
    const minute = Math.floor((this.time % 3600) / 60);
    const seconds = Math.floor((this.time % 3600) % 60);

    return `${this.min2digit(hour)}:${this.min2digit(minute)}:${this.min2digit(seconds)}`;
  }

  private min2digit(num: number): string {
    if (num < 10) {
      return `0${num}`;
    } else {
      return `${num}`;
    }
  }
}
