import { Component, OnInit } from '@angular/core';
import { FuriganaModel } from '../model/furigana.model';
import { HiraganaRomajiTimerGame } from '../game/hiragana-romaji-timer.game';
import { NihongoGame } from '../game/nihongo-base/nihongo-game';

@Component({
  selector: 'app-hiragana-romaji-timer',
  templateUrl: './hiragana-romaji-timer.component.html',
  styleUrls: ['./hiragana-romaji-timer.component.scss']
})
export class HiraganaRomajiTimerComponent implements OnInit {
  public hiraganaGame: NihongoGame = new HiraganaRomajiTimerGame();

  constructor() { }

  ngOnInit(): void { }

  get hiraganaTable(): FuriganaModel[] {
    return this.hiraganaGame.getTable();
  }

  isEnabled(furigana: FuriganaModel): boolean {
    const selected = this.hiraganaGame.getSelected().filter((f) => f.romaji === furigana.romaji);
    return this.hiraganaGame.hasStarted && selected.length === 0;
  }

  isVisible(furigana: FuriganaModel): boolean {
    const selected = this.hiraganaGame.getSelected().filter((f) => f.romaji === furigana.romaji);
    return !this.hiraganaGame.hasStarted || (this.hiraganaGame.hasStarted && selected.length > 0);
  }

  onSelect(furigana: FuriganaModel): void {
    this.hiraganaGame.checkSelected(furigana);
  }
}
